
.project-container {
  position: relative;
  z-index: -1;
  max-width: auto;
}

.card {
  margin-bottom: 10%;
  border: black;
  box-shadow: 30px 30px 30px black, 15px 10px 15px 10px #6c8508;
}

.card-img-top {
  height: 250px;
  filter: grayscale(80%);
  opacity: 0.9;
  box-shadow: 12px 29px 81px 0px rgba(161, 155, 155, 0.75),
    5px 5px 5px 5px black;
  border: 2px solid black;
}


.card-body {
  background-color: #000000d9;
}

.card-text,
.card-title {
  font-family: Arial, Helvetica, sans-serif;
}

.card-title {
  text-shadow: 2px 5px black;
}
.card-text {
  color: rgb(190, 186, 189);
  font-family: "Patrick Hand", cursive;
  font-size: 24px;
}

.sub-text {
  font-size: 16px;
}

