a.btn.btn-secondary {
  background-color: rgba(78, 72, 72, 0.75);
  border: thin black solid;
  margin-right: 2%;
  width: 170px;
  box-shadow: 0 0 5px black;
}

a.btn.btn-secondary:hover {
  box-shadow: 0 0 15px #d3174f;
}

a.btn.btn-secondary:visited {
  color: rgba(161, 155, 155, 0.75);
}
